:root{
  --bkg-color:rgb(48, 55, 68);
  --dark-color:rgb(29,32,37); 
  --mid-color:rgb(86, 93, 111);
  --accent-color:rgb(98, 113, 184);
  --light-color:rgb(251, 251, 255);
  background:var(--mid-color);

  --blue-cli:rgb(96,175,237);
  --red-cli:rgb(223,107,115);
  --yellow-cli:rgb(227,190,126);
}

.App {
  box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 20%);
  background:var(--bkg-color);
  max-width:1050px;
  width:calc(90vw - min(600px, 16vw));
  padding-top:3vh;
  margin:auto;
  min-height:100vh;
  padding-left:min(300px, 8vw);
  padding-right:min(300px, 8vw);
}

h1 {
  font-size:min(57pt, 10vw);
  color:var(--light-color);
  font-family:"League Spartan", sans-serif;
  margin-bottom:0em;
}

.type-subheader {
    color:var(--light-color);
    font-family: 'Courier New', Courier, monospace;
    font-size:min(22pt, 3.85vw);
    font-weight:100;
}

.App-menu-container{
  width:80%;
  display: flex; 
  justify-content: space-between; 
  flex-wrap: wrap; 
  color:var(--light-color);
  font-family: 'Courier New', Courier, monospace;
  font-size: min(14pt, 3.0vw);
  margin: 1em auto 1em auto;
}

.App-menu-option{
  width:100vw;
  max-width: 200px;
  text-align:center;
  cursor:pointer;
  transition: transform 100ms;
  height:1.5em;
  padding:1em;
  margin:auto;
}

.App-menu-option:hover{
  opacity:.75;
  transform: scale(1.1);
}

.App-endmatter{
  text-align:center;
  color:var(--light-color);
  opacity:.75;
}

.random-letter{
  opacity:.5;
}

.custom-console{
  width:100%;
  min-height:10em;
  max-width: 840px;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 20px 0px;
  font-size:min(12pt, 3vw);
}
.custom-console-header{
  width:calc(100% - 2em);
  margin:auto;
  border-radius:3px 3px 0px 0px;
  background:rgb(35, 35, 42);
  color:var(--light-color);
  padding: .5em 1em .5em 1em;
  font-family:'Courier New', Courier, monospace;
  font-size:min(12pt, 3vw);
}

.custom-console-body{
  width:calc(100% - 2em);
  margin:auto; 
  border-radius:0px 0px 3px 3px; 
  background:rgb(20, 20, 24); 
  color:var(--light-color); 
  padding: .5em 1em 3em 1em;
  font-family:'Courier New', Courier, monospace;
  min-height:500px;
  font-size:min(12pt, 3vw);
}

.custom-console-button-blue{
  color:var(--blue-cli);
   cursor:pointer;
}

.custom-console-button-red{
  color:var(--red-cli);
   cursor:pointer;
}

.custom-console-button-yellow{
  color:var(--yellow-cli);
   cursor:pointer;
}

.custom-console-button-gray{
  color:lightgray;
  opacity:.75;
  cursor:pointer;
}

.custom-console-blue{
  color:var(--blue-cli);
}

.custom-console-red{
  color:var(--red-cli);
}

.custom-console-yellow{
  coloR:var(--yellow-cli);
}